body {
	min-width: 320px;
	// font: 20px/1.22  $LatoLight, helvetica, sans-serif;
	font: 20px/1.22  $LatoRegular, sans-serif;
	color: $dark-grey;
	background: $white;
}

button,
[type=reset],
[type=submit],
[type=image],
a {
	-webkit-transition: background-color .2s, color .2s;
	transition: background-color .2s, color .2s;
}

img {
	display: block;
	height: auto;
	max-width: 100%;
}

b,
strong {
	font-weight: 700;
}

i,
em {
	font-style: italic;
}

p {
	margin: 0 0 15px;
}

%list-reset {
	list-style: none;
	margin: 0;
	padding: 0;
}

%clearfix {
	&:after {
		content: '';
		display: table;
		clear: both;
	}
}

.container {
	max-width: 1280px;
	margin: 0 auto;
	padding: 0 20px;
	&-md {
		max-width: 1150px;
		margin: 0 auto;
		padding: 0 20px;
	}
	&-sm {
		max-width: 1060px;
		margin: 0 auto;
		padding: 0 20px;
	}
	&-esm {
		max-width: 880px;
		margin: 0 auto;
		padding: 0 20px;
	}
	&-cnt {
		padding: 0 50px;
	}
}

.text-center {
	text-align: center;
}

h1,
h2 {
	font-size: 30px;
	line-height: 1.2;
	font-weight: 600;
	margin: 0 0 30px;
	// &.title-decor {
	// 	position: relative;
	// 	padding-bottom: 27px;
	// 	text-align: center;
