	// 		bottom: 0;
	// 		left:50%;
	// 		transform: translate(-50%, 0);
	// 		content: '';
	// 	}
	// }
}
h3 {
	font-size: 25px;
	font-weight: 600;
}
h6 {
	font-size: 16px;
	font-weight: 600;
}

// .ws-nowrap {
// 	white-space: nowrap;
// }

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0,0,0,0);
	border: 0;
}

@media (max-width: 1023px) {
	body {
		font-size: 16px;
	}
	h1,
	h2 {
		font-size: 25px;
	}
	h3 {
		font-size: 21px;
	}
}

@media (max-width: 767px) {
	body {
		font-size: 14px;
	}
	h1,
	h2 {
		font-size: 21px;
		// &.title-decor {
		// 	padding-bottom: 15px;
		// 	margin-bottom: 25px;
		// }
	}
	h3 {
		font-size: 18px;
	}
}

